import { useState } from "react";
import { cn } from "@/utils";
import { Button } from "./ui/Button";
import { Input } from "./ui/Input";
import { useBedrockPassport } from "@/hooks/useBedrockPassport";

interface EmailRegistrationProps {
  buttonClass?: string;
  errorClass?: string;
  successClass?: string;
  inputClass?: string;
  formClass?: string;
}

export const EmailRegistration = (
  props: EmailRegistrationProps,
): JSX.Element => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const { registerEmail, loginCallback } = useBedrockPassport();

  const [error, setError] = useState<string>("");
  const loginWEmail = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(""); // Clear previous errors

    //password must have at least 8 characters and contain at least one uppercase letter, one lowercase letter, one number and one special characters
    const passwordRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/;
    if (!password.match(passwordRegex)) {
      setError(
        "Password must have at least 8 characters and contain at least one uppercase letter, one lowercase letter, one number and one special characters",
      );
      return;
    }

    if (password !== passwordConfirm) {
      setError("Password does not match");
      return;
    }

    try {
      const res = await registerEmail(email, password, passwordConfirm);

      if (!res || !res.token) {
        setError(
          "It seems like the email is already registered. Please continue with reset passwor process if you still having problem",
        );
        return;
      }

      const { accessToken, refreshToken } = res.token;

      await loginCallback(accessToken, refreshToken);
    } catch (error) {
      console.error("Login failed:", error);
      setError(
        error instanceof Error
          ? error.message
          : "Login failed. Please try again.",
      );
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    await loginWEmail(e);
  };

  return (
    <form onSubmit={handleSubmit} className={cn("space-y-4", props.formClass)}>
      {error && (
        <div className={cn("text-red-500", props.errorClass)}>{error}</div>
      )}
      <div className="space-y-2">
        <Input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className={cn(props.inputClass)}
          required
        />
        <Input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className={cn(props.inputClass)}
          required
        />
        <Input
          type="password"
          placeholder="Password Confirmation"
          value={passwordConfirm}
          onChange={(e) => setPasswordConfirm(e.target.value)}
          className={cn(props.inputClass)}
          required
        />
      </div>
      <div className="flex gap-2">
        <Button
          type="submit"
          variant="outline"
          className={cn(props.buttonClass)}
        >
          Register
        </Button>
      </div>
    </form>
  );
};
