import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from "./ui/Card";
import { Button } from "./ui/Button";
import { UserProvider } from "@/typings/user";
import { Separator } from "./ui/Separator";
import { cn } from "@/utils";
import { useBedrockPassport } from "@/hooks/useBedrockPassport";
import { Icons } from "./ui/Icons";
import WalletLink from "./WalletLink";
import { defaultFeatures, type FeatureConfig } from "@/config/features";
import { EmailLogin } from "./EmailLogin";
import { useState } from "react";
import { EmailRegistration } from "./EmailRegister";
import { EmailReset } from "./EmailReset";
import { CurrencyPanel } from "./Currency";

interface LoginProps {
  //main settings
  title?: string;
  logo?: string;
  logoAlt?: string;
  walletButtonText?: string;
  separatorText?: string;
  authTitle?: string;

  //features flag
  features?: FeatureConfig;

  //start styling
  // Panel styling
  panelClass?: string;

  // Header section styling
  headerClass?: string;
  titleClass?: string;
  logoClass?: string;

  // Button styling
  buttonClass?: string;

  // Separator styling
  separatorClass?: string;
  separatorTextClass?: string;

  // Authentication method styling
  authTitleClass?: string;

  // Provider buttons styling
  googleButtonClass?: string;
  appleButtonClass?: string;
  emailButtonClass?: string;
  walletButtonClass?: string;

  // Form elements styling
  formClass?: string;
  inputClass?: string;
  errorClass?: string;
  successClass?: string;

  // User info display styling
  profileButtonClass?: string;
  logoutButtonClass?: string;
  linkRowClass?: string;
  walletRowClass?: string;
}

export const LoginPanel = ({ features, ...props }: LoginProps): JSX.Element => {
  const featureFlags = { ...defaultFeatures, ...features };

  const {
    signIn,
    user,
    linkSocial,
    widgetText,
    widgetUrl,
    signOut,
    linkProfile,
    isLoggedIn,
  } = useBedrockPassport();
  const [showEmailForm, setShowEmailForm] = useState(false);
  const [showRegistrationForm, setShowRegistrationForm] = useState(false);
  const [showResetForm, setShowResetForm] = useState(false);

  const loginWGoogle = () => {
    if (user?.ethAddress) {
      linkSocial(UserProvider.GOOGLE);
    } else {
      signIn(UserProvider.GOOGLE);
    }
  };

  const loginWApple = () => {
    if (user?.ethAddress) {
      linkSocial(UserProvider.APPLE);
    } else {
      signIn(UserProvider.APPLE);
    }
  };

  if (
    !featureFlags.enableAppleLogin &&
    !featureFlags.enableGoogleLogin &&
    !featureFlags.enableEmailLogin &&
    !featureFlags.enableWalletConnect
  ) {
    return (
      <Card className={cn("min-w-60", props.panelClass)}>
        <CardHeader>
          <div className={cn("flex items-center", props.headerClass)}>
            <CardTitle className={cn(props.titleClass)}>
              {props.title ?? "Sign In to"}
            </CardTitle>
            {props.logo && (
              <img
                src={props.logo}
                alt={props.logoAlt}
                className={cn(props.logoClass)}
              />
            )}
          </div>
        </CardHeader>
        <CardContent className="grid gap-4">
          You will need to enable at least one login method
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className={cn("min-w-60", props.panelClass)}>
      <CardHeader>
        <div className={cn("flex items-center", props.headerClass)}>
          <CardTitle className={cn(props.titleClass)}>
            {props.title ?? "Sign In to"}
          </CardTitle>
          {props.logo && (
            <img
              src={props.logo}
              alt={props.logoAlt}
              className={cn(props.logoClass)}
            />
          )}
        </div>
      </CardHeader>
      <CardContent className="grid gap-4">
        {user?.email ? (
          <>
            <div className="flex flex-row gap-4 justify-center items-center">
              {user.provider === UserProvider.APPLE ? (
                <Button
                  variant="outline"
                  className={cn(props.appleButtonClass)}
                  disabled
                >
                  <Icons.apple />
                </Button>
              ) : (
                <Button
                  variant="outline"
                  className={cn(props.googleButtonClass)}
                  disabled
                >
                  <Icons.google />
                </Button>
              )}
              <div>{user?.email}</div>
            </div>
          </>
        ) : !showEmailForm && !showRegistrationForm && !showResetForm ? (
          <div className="flex flex-col gap-4">
            <h4
              className={cn(
                "text-white text-center font-bold text-sm",
                props.authTitleClass,
              )}
            >
              {props.authTitle ?? "Continue with"}
            </h4>

            {/* SOCIAL WALLET LOGIN */}

            <div className="grid auto-cols-fr grid-flow-col gap-2 ">
              {featureFlags.enableGoogleLogin && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass, props.googleButtonClass)}
                  onClick={() => loginWGoogle()}
                >
                  <Icons.google />
                </Button>
              )}
              {featureFlags.enableAppleLogin && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass, props.appleButtonClass)}
                  onClick={() => loginWApple()}
                >
                  <Icons.apple />
                </Button>
              )}
              {featureFlags.enableEmailLogin && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass, props.emailButtonClass)}
                  onClick={() => setShowEmailForm(true)}
                >
                  <Icons.email />
                </Button>
              )}
            </div>
          </div>
        ) : (
          <>
            {showRegistrationForm && (
              <EmailRegistration
                buttonClass={props.buttonClass}
                inputClass={props.inputClass}
                successClass={props.successClass}
                errorClass={props.errorClass}
                formClass={props.formClass}
              />
            )}
            {showEmailForm && (
              <EmailLogin
                buttonClass={props.buttonClass}
                inputClass={props.inputClass}
                successClass={props.successClass}
                errorClass={props.errorClass}
                formClass={props.formClass}
              />
            )}

            {showResetForm && (
              <EmailReset
                buttonClass={props.buttonClass}
                separatorClass={props.separatorTextClass}
                inputClass={props.inputClass}
                successClass={props.successClass}
                errorClass={props.errorClass}
                formClass={props.formClass}
              />
            )}
          </>
        )}
        {showEmailForm || showRegistrationForm || showResetForm ? (
          <>
            <div className="relative">
              <Separator
                className={cn("my-4", "bg-white", props.separatorClass)}
                orientation="horizontal"
              />
              {!isLoggedIn && (
                <span
                  className={cn(
                    "absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 flex justify-center items-center py-1 px-2",
                    "bg-black",
                    props.separatorTextClass,
                  )}
                >
                  {props.separatorText ?? "or"}
                </span>
              )}
            </div>
            <div className="flex items-center justify-center w-full gap-2">
              <Button
                type="button"
                variant="outline"
                className={cn(props.buttonClass)}
                onClick={() => {
                  setShowEmailForm(false);
                  setShowRegistrationForm(false);
                  setShowResetForm(false);
                }}
              >
                Back
              </Button>

              <Button
                type="button"
                variant="outline"
                className={cn(props.buttonClass)}
                onClick={() => {
                  if (showResetForm) {
                    setShowEmailForm(false);
                    setShowRegistrationForm(true);
                    setShowResetForm(false);
                    return;
                  }

                  setShowEmailForm(!showEmailForm);
                  setShowRegistrationForm(!showRegistrationForm);
                }}
              >
                {showRegistrationForm ? "Sign In" : "Register"}
              </Button>
              {!showResetForm && (
                <Button
                  type="button"
                  variant="outline"
                  className={cn(props.buttonClass)}
                  onClick={() => {
                    setShowEmailForm(false);
                    setShowRegistrationForm(false);
                    setShowResetForm(true);
                  }}
                >
                  Reset
                </Button>
              )}
            </div>
          </>
        ) : (
          <>
            {featureFlags.enableWalletConnect && (
              <>
                {(featureFlags.enableAppleLogin ||
                  featureFlags.enableGoogleLogin ||
                  featureFlags.enableEmailLogin) && (
                    <div className="relative">
                      <Separator
                        className={cn("my-4", "bg-white", props.separatorClass)}
                        orientation="horizontal"
                      />
                      {!isLoggedIn && (
                        <span
                          className={cn(
                            "absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 flex justify-center items-center py-1 px-2",
                            "bg-black",
                            props.separatorTextClass,
                          )}
                        >
                          {props.separatorText ?? "or"}
                        </span>
                      )}
                    </div>
                  )}
                <div
                  className={cn(
                    "flex items-center justify-center w-full",
                    props.walletRowClass,
                  )}
                >
                  <WalletLink
                    className={cn(props.walletButtonClass ?? props.buttonClass)}
                    connectText={props.walletButtonText}
                  />
                </div>
              </>
            )}
          </>
        )}
        {isLoggedIn && (
          <>
            <div
              className={cn(
                "flex justify-start items-center w-full gap-2",
                props.linkRowClass,
              )}
            >
              {featureFlags.showProfileButton && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass, props.profileButtonClass)}
                  onClick={linkProfile}
                >
                  Profile
                </Button>
              )}
              {featureFlags.showLogoutButton && (
                <Button
                  variant="outline"
                  onClick={signOut}
                  className={cn(props.buttonClass, props.logoutButtonClass)}
                >
                  Logout
                </Button>
              )}
            </div>
            {featureFlags.enableCurrencyDisplay && (
              <div
                className={cn(
                  "flex justify-start items-center w-full gap-2",
                  props.linkRowClass,
                )}
              >
                <CurrencyPanel />
              </div>
            )}
          </>
        )}
      </CardContent>
      <CardFooter className="flex justify-center">
        <a href={widgetUrl} rel="noreferrer nofollow" target="_blank">
          <h4 className="text-white text-center font-bold text-sm">
            {widgetText}
          </h4>
        </a>
      </CardFooter>
    </Card>
  );
};
