import { UserProvider } from "../typings/user";

export const getLoginUrl = (
  baseUrl: string,
  service: UserProvider,
  callbackUrl: string,
  redirectPage?: string,
  referrer?: string,
) => {
  return `${baseUrl}/api/v1/auth/${service}?state=${callbackUrl}${redirectPage ? redirectPage : ""}${referrer ? `%26r=${referrer}` : ""}`;
};

export const getLinkUrl = (
  baseUrl: string,
  service: UserProvider,
  callbackUrl: string,
  redirectPage?: string,
  referrer?: string,
) => {
  return `${baseUrl}/api/v1/auth/identifier/${service}?state=${callbackUrl}${redirectPage ? redirectPage : ""}${referrer ? `%26r=${referrer}` : ""}`;
};
