import { useBedrockPassport } from "@/hooks/useBedrockPassport";


export const CurrencyPanel = (): JSX.Element => {
  const {
    currency
  } = useBedrockPassport();

  let listItems;
  if (currency) {
    listItems = currency?.Items?.map(item => <li><p>Bedrock Points: {item.Amount}</p></li>);
  }

  return (
    <div>
      <div><ul>{listItems}</ul></div>
    </div>
  )
}
