"use client";
import axios, {
  AxiosError,
  AxiosInstance,
  InternalAxiosRequestConfig,
} from "axios";

import { useState, useEffect, useCallback } from "react";
import { User, UserLoginDto, UserProvider, Widget } from "@/typings/user";
import { getLinkUrl, getLoginUrl } from "@/utils/login";
import { useBedrockToken } from "@/hooks/useToken";
import { Web3Provider } from "./Web3Provider";
import { NonceDto } from "@/typings/wallet";

import * as jose from "jose";
import { JwtPayload } from "@/typings/token";
import { UserContext } from "@/context/BedrockContext";
import { ItemsList } from "@/typings/items";

interface EmailCredentials {
  email: string;
  password: string;
}

export interface BedrockPassportIdInterface {
  user?: User;
  isLoggedIn: boolean;
  signIn: (
    provider: UserProvider,
    credentials?: EmailCredentials,
  ) => Promise<UserLoginDto | undefined>;
  loginCallback: (
    accessToken: string,
    refreshToken: string,
  ) => Promise<boolean>;
  signOut: () => void;
  accessToken?: string;

  getNonce: () => Promise<NonceDto | undefined>;
  verifyNonce: (
    message: unknown,
    signature: string,
  ) => Promise<UserLoginDto | undefined>;
  linkWallet: (
    message: unknown,
    signature: string,
  ) => Promise<UserLoginDto | undefined>;
  linkSocial: (provider: UserProvider) => void;
  errorWallet?: string;
  setErrorWallet: (data: string) => void;
  widgetText?: string;
  widgetUrl?: string;
  linkProfile: () => void;
  registerEmail: (
    email: string,
    password: string,
    passwordConfirm: string,
  ) => Promise<UserLoginDto | undefined>;
  getTokenReset: (email: string) => Promise<{
    code: number;
    status: string;
  }>;
  changePassword: (
    password: string,
    passwordConfirm: string,
    token: string,
  ) => void;
  getCurrency: () => Promise<ItemsList | undefined>;
  getItems: () => Promise<ItemsList | undefined>;
  currency?: ItemsList;
  apiClient: AxiosInstance;
}

interface Props {
  authCallbackUrl: string;
  baseUrl: string;
  children?: React.ReactNode;
  tenantId?: string;
  walletConnectId?: string;
  appName?: string;
  appDescription?: string;
  appUrl?: string;
  appIcon?: string;
  subscriptionKey?: string;
}


export const BedrockPassportProvider = (props: Props): JSX.Element => {

  const [initializing, setInitializing] = useState(true);

  const [widgetText, setWidgetText] = useState("Built on Bedrock");
  const [widgetUrl, setWidgetUrl] = useState("https://bedrockpassport.com");
  const { baseUrl, authCallbackUrl, tenantId } = props;

  const [errorWallet, setErrorWallet] = useState("");

  // Init API Client for the Passport API
  const apiClient = axios.create({
    baseURL: `${baseUrl}/api`,
  });

  const [user, setUser] = useState<User>();
  const [currency, setCurrency] = useState<ItemsList>();

  const { accessToken, refreshToken, login, logout } = useBedrockToken();

  const interceptorCallback = async (config: InternalAxiosRequestConfig) => {
    try {
      let currentAccessToken = accessToken();

      if (currentAccessToken) {
        const decoded = jose.decodeJwt<JwtPayload>(currentAccessToken);
        if (decoded.exp && decoded.exp * 1000 < Date.now()) {
          const currRefreshToken = refreshToken();
          if (!currRefreshToken) {
            logout();
            return config;
          }
          const newAccessToken = await axios
            .post<{ accessToken: string }>(`${baseUrl}/api/v1/auth/refresh`, {
              refreshToken: currRefreshToken,
              provider: decoded.provider,
            })
            .then((response) => response.data.accessToken)
            .catch((error: AxiosError) => {
              console.error(error.cause);
              return undefined;
            });

          if (!newAccessToken) {
            logout();
            return config;
          }

          if (newAccessToken) {
            login(newAccessToken, currRefreshToken);
            currentAccessToken = accessToken();
          }
        }
      }

      config.headers.Authorization = `Bearer ${currentAccessToken}`;
    } catch (e) {
      console.error("[interceptor.error] ", { e });
    }
    return config;
  };

  apiClient.defaults.headers.common["Ocp-Apim-Subscription-Key"] =
    props.subscriptionKey ?? "DEMO_BEDROCK_PASSPORT_KEY";

  apiClient.interceptors.request.use(interceptorCallback);

  const signIn = async (
    provider: UserProvider,
    credentials?: EmailCredentials,
  ) => {
    if (provider === UserProvider.EMAIL && credentials) {
      const response = await apiClient
        .post<UserLoginDto>(`/v1/auth/email/login`, {
          email: credentials.email,
          password: credentials.password,
        })
        .then((response) => {
          return response.data;
        })
        .catch(
          (
            error: AxiosError<{
              message: string;
            }>,
          ) => {
            console.error(error.cause);
            throw new Error(error.response?.data?.message ?? "Login failed");
          },
        );

      return response;
    } else {
      const redirectUrl = getLoginUrl(baseUrl, provider, authCallbackUrl);

      window.location.href = `${redirectUrl}${tenantId ? `&tenantId=${tenantId}` : ""}`;
    }
  };

  const getUser = useCallback(async () => {
    return apiClient
      .get<User>(`/v1/auth/user`, {})
      .then((response) => response.data)
      .catch((error: AxiosError) => {
        console.error(error.toJSON);
        return undefined;
      });
  }, [apiClient]);

  const getWidget = useCallback(async () => {
    return apiClient
      .get<Widget>(`/v1/auth/widget`)
      .then((response) => response.data)
      .catch((error: AxiosError) => {
        console.error(error.toJSON);
        return undefined;
      });
  }, [apiClient]);

  const loginCallback = useCallback(
    async (accessToken: string, refreshToken: string) => {
      if (!accessToken) {
        return false;
      }

      if (!refreshToken) {
        return false;
      }
      login(accessToken, refreshToken);

      const userData = await getUser();

      if (!userData) {
        logout();
        return false;
      }

      if (userData) {
        setUser(userData);
        return true;
      }

      return false;
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [],
  );

  const signOut = () => {
    logout();
    setUser(undefined);
    setCurrency(undefined);
    setInitializing(true);
  };

  useEffect(
    () => {
      console.log("[mount.start]: Bedrock Passport");
      const fetchUser = async () => {
        const userData = await getUser();
        if (userData) {
          setUser(userData);
        }
      };

      const fetchWidget = async () => {
        const widgetData = await getWidget();
        if (widgetData) {
          setWidgetText(widgetData.text);
          setWidgetUrl(widgetData.url);
        }
      };

      const fetchCurrency = async () => {
        const currencyData = await getCurrency();
        if (currencyData) {
          setCurrency(currencyData);
        }
      };

      if (initializing) {
        setInitializing(false);
        fetchWidget();
        if (accessToken()) {
          console.info("[INIT]: Token found");
          fetchUser();
          fetchCurrency();
        }
      }

      return () => {
        console.log("[mount.finish]: Bedrock Passport Provider");
      };
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [accessToken, initializing],
  );

  const getNonce = useCallback(async () => {
    return apiClient
      .get<NonceDto>(`/v1/message/nonce`)
      .then((response) => response.data)
      .catch((error: AxiosError) => {
        console.error(error.toJSON);
        return undefined;
      });
  }, [apiClient]);

  const verifyNonce = useCallback(
    async (message: unknown, signature: string) => {
      return apiClient
        .post<UserLoginDto>(`/v1/message/verify`, {
          message,
          signature,
          tenantId,
        })
        .then((response) => response.data)
        .catch((error: AxiosError) => {
          console.error(error.toJSON);
          return undefined;
        });
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [apiClient],
  );

  const linkSocial = (provider: UserProvider) => {
    const redirectUrl = getLinkUrl(baseUrl, provider, authCallbackUrl);

    window.location.href = `${redirectUrl}&token=${accessToken()}${tenantId ? `&tenantId=${tenantId}` : ""}`;
  };

  const linkWallet = useCallback(
    async (message: unknown, signature: string) => {
      console.log("[passport.link.wallet]");

      setErrorWallet("");
      return apiClient
        .post<UserLoginDto>(`/v1/auth/identifier/wallet`, {
          message,
          signature,
        })
        .then((response) => {
          return response.data;
        })
        .catch((error: AxiosError) => {
          console.error(error.message);
          setErrorWallet("Wallet already used");
          return undefined;
        });
    },
    [apiClient],
  );

  const linkProfile = () => {
    const redirectUrl = "https://bedrockpassport.com/login";

    window.location.href = `${redirectUrl}?token=${accessToken()}&refreshToken=${refreshToken()}`;
  };

  const registerEmail = async (
    email: string,
    password: string,
    passwordConfirm: string,
  ) => {
    try {
      const response = await apiClient
        .post<UserLoginDto>(`/v1/auth/email/register`, {
          email,
          password,
          passwordConfirm,
        })
        .then((response) => {
          return response.data;
        })
        .catch((error: AxiosError) => {
          console.error(error.message);
          throw new Error("[BRP-005] Email registration failed");
        });

      return response;
    } catch (error) {
      console.error("[Bedrock Passport] Email registration error:", error);
      throw error;
    }
  };

  const getTokenReset = async (email: string) => {
    try {
      const response = await apiClient.post<{ code: number; status: string }>(
        `/v1/auth/email/reset`,
        {
          email,
        },
      );

      if (!response.data) {
        throw new Error("[BRP-005] Email registration failed");
      }

      return response.data;
    } catch (error) {
      console.error("[Bedrock Passport] Email registration error:", error);
      throw error;
    }
  };

  const changePassword = async (
    password: string,
    passwordConfirm: string,
    token: string,
  ) => {
    try {
      const response = await apiClient.post<UserLoginDto>(
        `/v1/auth/email/password`,
        {
          password,
          passwordConfirm,
          token,
        },
      );

      if (!response.data) {
        throw new Error("[BRP-007] password change failed");
      }

      return response.data;
    } catch (error) {
      console.error("[Bedrock Passport] password change error:", error);
      throw error;
    }
  };
  const getCurrency = useCallback(async () => {
    return apiClient
      .post<ItemsList>(`/v1/economy/currency`, {
        titleId: tenantId,
        count: 10,
        inventory: "default",
      })
      .then((response) => response.data)
      .catch((error: AxiosError) => {
        console.error(error.toJSON);
        return undefined;
      });
  }, [apiClient, tenantId]);

  const getItems = useCallback(async () => {
    return apiClient
      .post<ItemsList>(`/v1/economy/inventoryItems`, {
        titleId: tenantId,
        count: 10,
        inventory: "default",
      })
      .then((response) => response.data)
      .catch((error: AxiosError) => {
        console.error(error.toJSON);
        return undefined;
      });
  }, [apiClient, tenantId]);

  const value: BedrockPassportIdInterface = {
    user,
    signIn,
    signOut,
    loginCallback,
    accessToken: accessToken(),
    isLoggedIn: !!accessToken(),
    getNonce,
    verifyNonce,
    linkSocial,
    linkWallet,
    errorWallet,
    setErrorWallet,
    widgetText,
    widgetUrl,
    linkProfile,
    registerEmail,
    getTokenReset,
    changePassword,
    getCurrency,
    getItems,
    currency,
    apiClient,
  };

  return (
    <UserContext.Provider value={value}>
      <Web3Provider
        walletConnectProjectId={
          props.walletConnectId ?? "c00e7bea048e69d5adaea82259539eb6"
        }
        appName={props.appName ?? "Bedrock Passport"}
        appDescription={props.appDescription}
        appUrl={props.appUrl}
        appIcon={props.appIcon}
        baseUrl={baseUrl}
        tenantId={tenantId}
      >
        {props.children}
      </Web3Provider>
    </UserContext.Provider>
  );
};
