import { ConnectKitButton } from "connectkit";
import { Button } from "./ui/Button";
import { Icons } from "./ui/Icons";
import { useBedrockPassport } from "../hooks/useBedrockPassport";
import { useSignMessage, useChainId, useDisconnect } from "wagmi";

import { SiweMessage } from "siwe";
import { truncate } from "@/utils";
import WalletAlert from "./WalletAlert";
import { useAccountEffect } from "@/hooks/useAccountEffect";

type Props = {
  className?: string;
  connectText?: string;
};

const WalletLink: React.FC<Props> = (props) => {
  const {
    getNonce,
    linkWallet,
    loginCallback,
    user,
    verifyNonce,
    isLoggedIn,
    errorWallet,
  } = useBedrockPassport();
  const { signMessageAsync, status: signStatus } = useSignMessage();

  const { disconnect } = useDisconnect();
  const chainId = useChainId();

  const signLink = async (address: string) => {
    console.log("Sign for Linking");

    const nonceDto = await getNonce();
    const message = new SiweMessage({
      domain: window.location.host,
      address,
      statement: "Sign in to Bedrock Passport",
      uri: window.location.origin,
      version: "1",
      nonce: nonceDto?.nonce,
      chainId,
    });

    const signature = await signMessageAsync({
      message: message.prepareMessage(),
    }).catch(() => {
      throw new Error("LOGIN_SIGN_ERROR");
    });

    const validate = await linkWallet(message, signature);

    if (!validate || !validate.token) {
      disconnect();
      throw new Error("LOGIN_VERIFY_ERROR");
    }

    await loginCallback(
      validate.token.accessToken,
      validate.token.refreshToken,
    );
  };

  const signLogin = async (address: string) => {
    console.log("Sign After Login");

    const nonceDto = await getNonce();
    const message = new SiweMessage({
      domain: window.location.host,
      address,
      statement: "Sign in to Bedrock Passport",
      uri: window.location.origin,
      version: "1",
      nonce: nonceDto?.nonce,
      chainId,
    });

    const signature = await signMessageAsync({
      message: message.prepareMessage(),
    }).catch(() => {
      throw new Error("LOGIN_SIGN_ERROR");
    });

    const validate = await verifyNonce(message, signature);

    if (!validate || !validate.token) {
      disconnect();
      throw new Error("LOGIN_VERIFY_ERROR");
    }

    await loginCallback(
      validate.token.accessToken,
      validate.token.refreshToken,
    );
  };

  useAccountEffect({
    async onConnect(data) {
      console.log("EFFECT_INIT_SIGN");
      if (
        isLoggedIn &&
        user?.ethAddress?.toLowerCase() !== data.address?.toLowerCase()
      ) {
        console.log("EFFECT_INIT_SIGN_LINK");
        await signLink(data.address)
          .then(() => {
            const event = new CustomEvent("Bedrock_Wallet_Linked", {
              detail: { address: data.address },
            });
            document.dispatchEvent(event);
          })
          .catch((e) => {
            console.error("EFFECT_INIT_SIGN_LINK", { e });
            disconnect();
          });
      } else {
        console.log("EFFECT_INIT_SIGN_LOGIN");
        if (!isLoggedIn) {
          await signLogin(data.address)
            .then(() => {
              const event = new CustomEvent("Bedrock_Wallet_Login", {
                detail: { address: data.address },
              });
              document.dispatchEvent(event);
            })
            .catch((e) => {
              console.error("EFFECT_INIT_SIGN_LOGIN", { e });
              disconnect();
            });
        }
      }
    },
  });

  return (
    <>
      <WalletAlert open={signStatus === "pending"} />
      <div className="grid grid-cols-1 gap-4">
        <ConnectKitButton.Custom>
          {({ isConnected, show, truncatedAddress, isConnecting }) => {
            return (
              <>
                <Button
                  onClick={show}
                  variant={"outline"}
                  className={props.className}
                  disabled={isConnecting}
                >
                  <Icons.wallet />
                  <span>
                    {isConnected
                      ? truncatedAddress
                      : user?.ethAddress
                        ? truncate(user.ethAddress)
                        : (props.connectText ?? "Connect")}
                  </span>
                </Button>
              </>
            );
          }}
        </ConnectKitButton.Custom>

        {errorWallet && <div className="text-red-600">{errorWallet}</div>}
      </div>
    </>
  );
};
export default WalletLink;
