import { useState } from "react";
import { cn } from "@/utils";
import { Button } from "./ui/Button";
import { Input } from "./ui/Input";
import { useBedrockPassport } from "@/hooks/useBedrockPassport";

interface EmailResetProps {
  buttonClass?: string;
  separatorClass?: string;
  errorClass?: string;
  successClass?: string;
  inputClass?: string;
  formClass?: string;
}
export const EmailReset = (props: EmailResetProps): JSX.Element => {
  const [email, setEmail] = useState("");

  const { getTokenReset } = useBedrockPassport();

  const [error, setError] = useState<string>("");

  const [success, setSuccess] = useState<string>("");

  const handleRequestToken = async () => {
    setError(""); // Clear previous errors
    if (!email) {
      setError("Please enter an email address");
      return;
    }

    //validate email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setError("Please enter a valid email address");
      return;
    }

    try {
      setSuccess("");
      console.log("requestToken", { email });

      const res = await getTokenReset(email);

      console.log("getTokenReset", { res });

      setSuccess("Please check your email to continue");
    } catch (error) {
      console.error("Request failed:", error);
      setError(
        "Please check if the email is correct. If the problem persists, please contact support.",
      );
    }
  };

  return (
    <div className={cn("space-y-4", props.formClass)}>
      {error && (
        <div className={cn("text-red-500", props.errorClass)}>{error}</div>
      )}

      {success && (
        <div className={cn("text-green-500", props.successClass)}>
          {success}
        </div>
      )}
      <div className="space-y-2">
        <Input
          type="email"
          placeholder="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className={cn(props.separatorClass, "")}
          required
        />

        <Button
          type="button"
          variant="outline"
          className={cn(props.buttonClass)}
          onClick={handleRequestToken}
        >
          Recover
        </Button>
      </div>
    </div>
  );
};
