import { WagmiProvider, createConfig, http } from "wagmi";
import {
  avalanche,
  arbitrum,
  base,
  bsc,
  mainnet,
  fantom,
  tron,
  apeChain,
  optimism,
  polygon,
} from "wagmi/chains";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { ConnectKitProvider, getDefaultConfig } from "connectkit";
import { ReactNode } from "react";
import { orangeTestnet } from "../custom-network/orange";

const queryClient = new QueryClient();

export interface Web3ProviderProps {
  walletConnectProjectId: string;
  appName: string;
  baseUrl: string;
  defaultChainId?: number;
  appDescription?: string;
  appUrl?: string;
  appIcon?: string;
  children?: ReactNode;
  tenantId?: string;
}


export const Web3Provider = ({ ...props }: Web3ProviderProps): JSX.Element => {

  const config = createConfig(
    getDefaultConfig({
      chains: [
        mainnet,
        avalanche,
        arbitrum,
        base,
        bsc,
        fantom,
        tron,
        apeChain,
        optimism,
        polygon,
        orangeTestnet,
      ],
      transports: {
        [mainnet.id]: http(),
        [avalanche.id]: http(),
        [arbitrum.id]: http(),
        [base.id]: http(),
        [bsc.id]: http(),
        [fantom.id]: http(),
        [tron.id]: http(),
        [apeChain.id]: http(),
        [optimism.id]: http(),
        [polygon.id]: http(),
        [orangeTestnet.id]: http(),
      },
      // Required API Keys
      walletConnectProjectId: props.walletConnectProjectId,
      appName: props.appName,

      // Optional App Info
      appDescription: props.appDescription ?? "",
      appUrl: props.appUrl ?? "",
      appIcon: props.appIcon ?? "",
    }),
  );

  return (
    <WagmiProvider config={config}>
      <QueryClientProvider client={queryClient}>
        <ConnectKitProvider
          options={{
            initialChainId: props.defaultChainId ?? 1,
            enforceSupportedChains: true,
          }}
        >
          {props.children}
        </ConnectKitProvider>
      </QueryClientProvider>
    </WagmiProvider>
  );
};
